<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import Section from "./_Section.svelte"
  import TogglableEditor from "../Quill/TogglableEditor.svelte"

  export item = undefined

  save = (e)->
    item.content = e.detail.html
    item.content_indexable = e.detail.text
    dispatch "update"


</script>



<Section {open} name="About">
  <TogglableEditor bind:content={item.content} text={item.content_indexable} height="calc(100vh - 200px)" on:save={save}/>
</Section>
