<script lang="coffeescript">

  import PageContent from '../_Common/PageContent.svelte'
  import PageHeader from '../_Common/PageHeader.svelte'
  import HomeProjectList from "../_Common/Sections/Home_ProjectList.svelte"

  import Project from '../../helpers/project.js'

  jump = (anchorName)->
    elem = u("[name=#{anchorName}]")
    scrollContainer = u("body > main > section").first()
    scrollContainer.scrollTop = elem.first().offsetTop - 40

  list = []
  rawList = []

  resource = new Project()

  getProjects = ()->
    projectLoadedScb = (projects)->
      list = projects
      # console.log projects
      rawList = projects.map (p)->
        return Object.select(p, ["id","assignments"]) # this creates a discrete copy of the object. array.clone() does not do that.

    resource.select(projectLoadedScb, null, null)

  getProjects()

  updateProject = (e)->
    scb =(updated)->
      getProjects()
    ecb = (err)->
      console.log "udpate err"
      console.log err
      getProjects()
    resource.updateAttributes e.detail, scb, ecb, null

  itemsWithDueDates = (projects)->
    output = []
    clonedProjects = projects.clone()
    output = clonedProjects.filter (project)->
      ["tasks","assignments","documents"].filter (collection)->
        project[collection] = project[collection].filter (collectionItem)->
          #console.log "collectionItme"
          #console.log collectionItem

          collectionItem.checklist_items = collectionItem.checklist_items.filter (checklist_item)->
            return checklist_item.due_date?

          return (collectionItem.checklist_items.length > 0) || collectionItem.due_date?
      return (project["tasks"].length > 0) || (project["assignments"].length > 0) || (project["documents"].length > 0) || project.due_date?

    return output


</script>

<style lang="sass">

  ul[slot="nav"]
    margin-top: 100px

    padding-left: 32px
    a
      cursor: pointer
    li
      padding: 4px 0px

  header
    h1
      font-size: 36px
      color: var(--darkest-blue)
    margin-bottom: var(--spacing-m)
  section
    margin: 0px 56px 24px 56px
</style>

<PageContent>
  <a name="top"></a>
  <PageHeader>Current Work</PageHeader>

  <section>
    <div>
      <HomeProjectList list={itemsWithDueDates(list)} on:update={updateProject} {rawList} on:refresh={getProjects}/>
    </div>
    <p>Current Work is any project, task, document, assignment or check list item that has a due date.</p>
  </section>
</PageContent>
