<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export item = undefined
  export value = { percent_complete: 0, color: "light-gray" }
  export size = 24

  newValue = undefined

  import { Row, RowCell } from "../../../components/Flex"
  import { Popover, Tooltip } from "../../../components/Overlay"
  import IconProgressCircle from "../../../components/Icon/IconProgressCircle.svelte"


  # menu

  show = false

  triggerClick = ()->
    dispatch "open"
    show = true

  triggerRef = undefined

  cancel = ()->
    show = false
    dispatch "close"
    newValue = value

  save = (val, color)->
    newValue = { percent_complete: val , color: color }
    show = false
    progressItem = { id: item.id, progress: newValue }
    if newValue == 100
      if value.percent_complete < 100 # then add completion date, don''t change it if it was already 100%
        progressItem["completion_date"] = Date.create()
    else
      if value.percent_complete == 100 # then clear completion date
        progressItem["completion_date"] = null
    dispatch "close"
    dispatch "update", progressItem

  `$: newValue = { percent_complete: value.percent_complete  , color: value.color }`

</script>

<div on:click={triggerClick} bind:this={triggerRef}>
  <IconProgressCircle bind:value={item.progress.percent_complete} bind:color={item.progress.color} {size}/>
</div>
<Popover bind:show={show} bind:trigger={triggerRef} on:close={cancel} width="160px">
  {#each ["green","yellow","red","blue","dark-gray","gray","light-gray"] as color}
    <Row style="padding: 8px">
      {#each [0,25,50,75,100] as percentage}
        <RowCell>
          <button type="button" on:click={save(percentage,color)} icon style="margin: 0px 2px">
            <IconProgressCircle value={percentage} {color}/>
          </button>
        </RowCell>
      {/each}
    </Row>
  {/each}
</Popover>
