<script lang="coffeescript">
  import { createEventDispatcher} from "svelte"
  dispatch = createEventDispatcher()

  export item = undefined
  export value = undefined

  newValue = undefined



  import Menu from "../Menus/_Assignments.svelte"
  import { Tooltip } from "../../../components/Overlay"


  import tippy from 'tippy.js'



  export project = undefined


  tippyObject = undefined
  component = undefined



  save = (change)->
    dispatch "update", change

  debouncedSave = save.debounce(500)


  handleMenuSelection = (selection)->
    switch selection
      when "all"
        # clear it
        if sortedList.length == (item.assigned_to || []).length
          item.assigned_to = []
        else
          item.assigned_to = sortedList.map (o)->
            return o.id

        component.$set { list: project.assignments, assigned_to: item.assigned_to }
        debouncedSave { id: item.id, assigned_to: item.assigned_to }
      else
        console.log "assign to individual"
        # push "#/projects/#{selection}"

        unless item.assigned_to?
          item.assigned_to = []

        if item.assigned_to.includes(selection)
          # remove it
          item.assigned_to.remove(selection)
        else
          item.assigned_to.push selection
        item.assigned_to = item.assigned_to.unique()
        component.$set { list: project.assignments, assigned_to: item.assigned_to }
        debouncedSave { id: item.id, assigned_to: item.assigned_to }

  popover = (element, content) ->
    tippyOptions =
      theme: "menu"
      appendTo: 'parent'
      trigger: 'click'
      placement: "bottom-end"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$on "selected",  (e) ->
          tippyObject.hide()
          handleMenuSelection e.detail

        component.$set { list: project.assignments, assigned_to: item.assigned_to }

        return

    else
      tippyOptions.content = content
    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        console.error "update"
        if component
          component.$set { list: list }

        return
      destroy: ->
        tippyObject.destroy()
        if component
          # debugger
          component.$destroy()
        return

    }





  # menu






  sort = (val)->
    if val?
      return val.sortBy "ordinal"
    else
      []

  match = (assigned_ids)->

    output = sort(project.assignments).filter (a)->
      return (assigned_ids || []).includes(a.id)
    output

  getTooltip = (v)->
    if (v.assigned_to || []).length == 0
      return "Unassigned"
    else
      options =  match(v.assigned_to).map (i)->
        return "<li>#{i.name}</li>"

      return "<ul>#{options}</ul>"




  `$: assigned = match(item.assigned_to)`
  `$: sortedList = sort(project.assignments)`
  `$: tooltip = getTooltip(item)`

</script>



<button icon use:popover={Menu}>

    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24">
      {#if (item.assigned_to || []).length == 0}
        <path class="fill" d="M19,3H14.82C14.4,1.84 13.3,1 12,1C10.7,1 9.6,1.84 9.18,3H5A2,2 0 0,0 3,5V19A2,2 0 0,0 5,21H19A2,2 0 0,0 21,19V5A2,2 0 0,0 19,3M12,3A1,1 0 0,1 13,4A1,1 0 0,1 12,5A1,1 0 0,1 11,4A1,1 0 0,1 12,3M7,7H17V5H19V19H5V5H7V7Z"/>
      {:else}
        <path class="fill" d="M19,3A2,2 0 0,1 21,5V19A2,2 0 0,1 19,21H5A2,2 0 0,1 3,19V5A2,2 0 0,1 5,3H9.18C9.6,1.84 10.7,1 12,1C13.3,1 14.4,1.84 14.82,3H19M12,3A1,1 0 0,0 11,4A1,1 0 0,0 12,5A1,1 0 0,0 13,4A1,1 0 0,0 12,3M7,7V5H5V19H19V5H17V7H7M12,9A2,2 0 0,1 14,11A2,2 0 0,1 12,13A2,2 0 0,1 10,11A2,2 0 0,1 12,9M8,17V16C8,14.9 9.79,14 12,14C14.21,14 16,14.9 16,16V17H8Z"/>
      {/if}
    </svg>

</button>
