<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import tippy from 'tippy.js'

  import Menu from './_Project.svelte'
  import DeleteConfirmation from '../../../components/Overlay/Modal/DeleteConfirmation.svelte'

  export item = undefined

  showDeleteConfirmation = false

  tippyObject = undefined
  component = undefined


  handleMenuSelection = (selection)->
    switch selection
      when "delete"
        showDeleteConfirmation = true
      else
        console.log "do nothing"


  popover = (element, content) ->
    tippyOptions =
      theme: "menu"
      appendTo: 'parent'
      trigger: "click"
      placement: "bottom-end"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$on "selected",  (e) ->
          tippyObject.hide()
          handleMenuSelection e.detail
        return

    else
      tippyOptions.content = content
    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        console.error "update"
        #if component
        #  component.$set { value: selectedDate }

        return
      destroy: ->
        tippyObject.destroy()
        if component
          # debugger
          component.$destroy()
        return

    }


  deleteConfirmationCancel = ()->
    showDeleteConfirmation = false

  destroy = ()->
    dispatch "destroy", item
    showDeleteConfirmation = false

</script>

<button type="button" icon use:popover={Menu}>
  <svg class="menu" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M12,16A2,2 0 0,1 14,18A2,2 0 0,1 12,20A2,2 0 0,1 10,18A2,2 0 0,1 12,16M12,10A2,2 0 0,1 14,12A2,2 0 0,1 12,14A2,2 0 0,1 10,12A2,2 0 0,1 12,10M12,4A2,2 0 0,1 14,6A2,2 0 0,1 12,8A2,2 0 0,1 10,6A2,2 0 0,1 12,4Z"/></svg>
</button>

{#if showDeleteConfirmation}
  <DeleteConfirmation on:destroy={destroy} on:cancel={deleteConfirmationCancel}/>
{/if}
