<script lang="coffeescript">

  import { createEventDispatcher, afterUpdate } from 'svelte'
  dispatch = createEventDispatcher()


  import tippy from 'tippy.js'
  import Calendar from "./Calendar.svelte"

  export value = Date.create()
  export style = undefined

  selectedDate = value.format("{yyyy}-{MM}-{dd}")
  tippyObject = undefined
  component = undefined

  popover = (element, content) ->

    tippyOptions =
      distance: '-20px'
      theme: "datepicker"
      trigger: "click"
      placement: "bottom"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$set { value: selectedDate }
        component.$on "change",  (e) ->
          console.error "changevalue"
          value = e.detail
          selectedDate = e.detail.format("{yyyy}-{MM}-{dd}")
          component.$set { value: selectedDate }
          dispatch("change", selectedDate)
          tippyObject.hide()
        return

    else
      tippyOptions.content = content
    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        console.error "update"
        if component
          component.$set { value: selectedDate }

        return
      destroy: ->
        tippyObject.destroy()
        if component
          # debugger
          component.$destroy()
        return

    }




</script>
<style global lang="sass">

</style>


<div layout="row">
  <div flex="initial" style="width:100px">
    <input type="text" bind:value={selectedDate} on:keyup/>
  </div>
  <div flex="initial" style="width: 40px">
    <button type="button" icon use:popover={Calendar}>
      <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M22 3h-3V1h-2v2H7V1H5v2H2v20h20V3zm-2 18H4V8h16v13z"/></svg>
    </button>
  </div>
</div>
