<script lang="coffeescript">
  import Indent from "../Indent.svelte"
  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()





  import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte'




  import DueDatePopover from '../Popovers/DueDate.svelte'
  import AssignmentPopover from '../Popovers/Assignment.svelte'





  import Menu from '../Menus/Checklist.svelte'


  export assignments = []
  export project = undefined

  # optional
  export showProgress = true
  export showAssignment = true
  export showChecklist = false



  export collection = []
  export item = undefined
  export rawList = []

  import Project from '../../../helpers/project.js'

  resource = new Project()




  style = ":hover {font-weight: bold}"

  save = ()-> # assignment



  sort = ()->
    if item.checklist_items?
      return item.checklist_items.sortBy "ordinal"
    else
      []


  style = ":hover {font-weight: bold}"

  toggleCompleted = (checklistItem)->
    # has some additional logic
    wasAlreadyCompleted = (checklistItem.is_completed == true)
    checklistItem.is_completed = !checklistItem.is_completed
    if checklistItem.isCompleted
      unless wasAlreadyCompleted # then don''t update date
        checklistItem.completion_date = Date.create()
    else # flip it back to null
      checklistItem.completion_date = null

    updateAttribute { detail: { id: checklistItem.id, is_completed: checklistItem.is_completed,  completion_date: checklistItem.completion_date} }



  updateAttribute = (e)->
    console.log "checklist updateAttribute"
    console.log e.detail
    change = e.detail
    changed = item.checklist_items.find { id: change.id }
    Object.merge(changed, change)


    item.checklist_items = item.checklist_items # refresh array

    changeOptions =
      project: project
      subDocument: item
      checklistItem: changed


    dispatch "update", changeOptions



  `$: list = sort(item.checklist_items)`
</script>



  {#if list.length > 0}

    <div>

      {#each list as checklistItem, index}

        <div layout="row" type="list-item">
          <Indent level={1} width={32}/>
          <div flex="initial" style="padding-right: 8px">
            <button type="button" icon on:click={toggleCompleted(checklistItem)}>
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/>
                {#if checklistItem.is_completed}
                  <path d="M21 3H3v18h18V3zM10 17l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z"/>
                {:else}
                  <path d="M19 5v14H5V5h14m2-2H3v18h18V3z"/>
                {/if}
              </svg>
            </button>
          </div>
            <div flex ellipsis>
              <InlineInput bind:value={checklistItem.content} on:update={updateAttribute({ detail: {id: checklistItem.id, name: checklistItem.content } })} {style} let:label>
              <span ellipsis>{label}</span>
            </InlineInput>
          </div>
          <div flex="initial">
            <div layout="row">



              <!-- due date menu -->
              <div flex="initial">
                <DueDatePopover item={checklistItem} value={checklistItem.due_date} on:update={updateAttribute} on:open on:close/>
              </div>


              <!-- assignment menu -->
              {#if (project.assignments.length > 0)}
                <div flex="initial" present={(checklistItem.assigned_to || []).length > 0}>
                  <AssignmentPopover {project} bind:item={checklistItem} on:update={updateAttribute} on:open on:close/>
                </div>
              {/if}

            </div>

          </div>

        </div>



      {/each}
    </div>
  {/if}

<style lang="sass">
  [type="list-item"]
    width: 100%
    margin-bottom: 8px
    [present],[present="false"]
      display: none
    [present="true"]
      display: flex
    &:hover
      [present]
        display: flex
</style>
