<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()




  import DraggableItem from "../Lists/Nestable/DraggableIndentableItem.svelte"
  import DroppableDivider from "../Lists/Nestable/DroppableDivider.svelte"

  import QuickEntry from "../QuickEntry/GenericItem.svelte"
  import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte'


  import DueDatePopover from '../Popovers/DueDate.svelte'
  import AssignmentPopover from '../Popovers/Assignment.svelte'


  import Section from "./_Section.svelte"

  import SubDocument from "../../../helpers/checklist_item.js"

  import HierarchicalList from "../../../helpers/hierarchical_list.js"


  import Menu from '../Menus/Checklist.svelte'
  import Indent from "../Indent.svelte"

  export assignments = []


  collection = "checklist_items"


  export item = undefined
  export project = undefined
  export listId = uuid()
  export sameListOnly = true


  subDocument = new SubDocument



  style = ":hover {font-weight: bold}"
  # drag and drop





  dropEvent = (e)->
    refresh = false

    tree = new HierarchicalList(item[collection])

    invalidDrop = tree.isInvalidDrop(e.detail)

    if invalidDrop?
      message = invalidDrop
      showToast = true
    else
      refresh = tree.dropEvent e.detail


    if refresh
      item[collection] = tree.list
      dispatch "update"


  # crud --

  update = ()->
    dispatch "update"


  add = (event)->
    console.log subDocument
    newRecord = subDocument.stub(event.detail)
    newRecord.ordinal = (item[collection] || []).length
    item[collection] = item[collection].add newRecord
    dispatch "update"

  saveName = (e)->
    change =
      id: item.id
      content: e.detail
    dispatch "update", change

  save = (e)->
    change = e.detail
    change["id"] = item.id
    dispatch "update", change


  toggleCompleted = (checklistItem)->
    found = item[collection].find {id: checklistItem.id}
    if found?
      found.is_completed = !found.is_completed
      found.completion_date = ` checklistItem.is_completed ? Date.create() : null`
    item[collection] = item[collection]
    dispatch "update"


  updateAttribute = (e)->
    change = e.detail
    changed = item[collection].find { id: change.id }
    Object.merge(changed, change)
    item[collection] = item[collection] # refresh array
    dispatch "update", change

  destroy = (e)->
    item[collection].remove {id: e.detail.id}
    item[collection] = item[collection]
    dispatch "update"
    showDeleteConfirmation = false


  sort = ()->
    if item[collection]?
      return item[collection].sortBy "ordinal"
    else
      []


  `$: list = sort(item[collection])`
</script>

<Section name={collection.titleize()} {open}>

  {#if list && list.length}

    <div>
      <DroppableDivider index={-1} {listId} {sameListOnly} on:drop={dropEvent} />
      {#each list as checklistItem, index}
        <DraggableItem item={checklistItem} index={index} {listId} {sameListOnly} on:drop={dropEvent} on:update={updateAttribute}>
        <div layout="row" type="list-item">
          <Indent level={(checklistItem.indent || 0).clamp(0,5)}/>
          <div flex="initial" style="padding-right: 8px">
            <button type="button" icon on:click={toggleCompleted(checklistItem)}>
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/>
                {#if checklistItem.is_completed}
                  <path d="M21 3H3v18h18V3zM10 17l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z"/>
                {:else}
                  <path d="M19 5v14H5V5h14m2-2H3v18h18V3z"/>
                {/if}
              </svg>
            </button>
          </div>
            <div flex ellipsis>
            <InlineInput bind:value={checklistItem.content} on:update={saveName} {style} let:label>
              <span ellipsis><a><em>{label}</em></a></span>
            </InlineInput>
          </div>
          <div flex="initial">
            <div layout="row">
              <div flex="initial">
                <DueDatePopover item={checklistItem} value={checklistItem.due_date} on:update={updateAttribute} on:open on:close/>
              </div>
              {#if (project.assignments.length > 0)}
                <div flex="initial" present={(checklistItem.assigned_to || []).length > 0}>
                  <AssignmentPopover {project} bind:item={checklistItem} on:update={updateAttribute} on:open on:close/>
                </div>
              {/if}
            </div>
          </div>
        </div>


          <div slot="menu">
            <Menu item={checklistItem} on:update={updateAttribute} on:destroy={destroy}/>
          </div>
        </DraggableItem>
        <DroppableDivider {item} index={index} {listId} {sameListOnly} on:drop={dropEvent} />
      {/each}
    </div>
  {/if}

  <QuickEntry on:add={add} stubPlaceholder="Add Checklist Item" attribute="content"/>




</Section>
<style lang="sass">
  [type="list-item"]
    width: 100%
    max-width: 100%
    [present]
      display: none
    [present="true"]
      display: flex
    [main]

    &:hover
      [present]
        display: flex

</style>
