  import ProjectList from '../_Common/Sections/ProjectList.svelte'

  list = []

  resource = new Project()

  getProjects = ()->
    scb = (projects)->
      list = projects
    resource.select(scb, null, null)

  getProjects()

  getProject = ()->
    scb = (doc) ->
      project = doc
    resource.selectOne(id, scb, null, null)

  destroy = (e)->
    scb = ()->
      getProjects()
    resource.destroy e.detail.id, scb, null, null

  updateProject = (e)->
    scb =(updated)->
      getProjects()
    ecb = (err)->
      getProjects()
    resource.updateAttributes e.detail, scb, ecb, null

