

<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {push, pop, replace, location} from 'svelte-spa-router'


  import Indent from "../Indent.svelte"



  import Menu from "../Menus/Project.svelte"

  import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte'
  


  import ProgressPopover from '../Popovers/Progress.svelte'
  import DueDatePopover from '../Popovers/DueDate.svelte'
  import EstimatePopover from '../Popovers/Time.svelte'

  import HomeSubDocument from "./Home_SubDocumentList.svelte"


  #
  #
  ###

  ###

  export list = []
  export rawList = []

  style = ":hover {font-weight: bold}"

  updateAttribute = (e)->
    # Update Project Attribute
    console.log "HomeProjectList.updateAttribute"
    console.log e.detail
    projectToUpdate = list.find { id: e.detail.id }
    if projectToUpdate?
      Object.merge projectToUpdate, e.detail
      list = list
      dispatch "update", e.detail # this will save the change to the proejct


  updateProject = (e)->
    console.log "updateProject"
    console.log e.detail
    dispatch "update", e.detail

  goToItem = (p)->
    console.log "gotoitem"
    console.log "#/projects/#{p.id}"
    push "#/projects/#{p.id}"

  refresh = ()->
    dispatch "refresh"

  `$: sortedList = list.sortBy("ordinal")`
</script>

{#if sortedList.length > 0}
  {#each sortedList as project }
    <section>
      <div layout="row" type="list-item">
        <div flex="initial" section="progress" style="padding-right: 8px; padding-top: 3px">
          <ProgressPopover item={project} bind:value={project.progress} on:update={updateAttribute} on:open on:close/>
        </div>

        <div flex ellipsis >
          <InlineInput bind:value={project.name} on:update={ updateAttribute({ detail: {id: project.id, name: project.name} }) } on:clicked={goToItem(project)} {style} let:label>
            <h3 ellipsis><a href="#/projects/{project.id}" on:click|preventDefault><em>{label || "No Name"}</em></a></h3>
          </InlineInput>
        </div>
        <div flex="initial" style="padding-left: 8px; padding-top: 3px">
          <div layout="row">
            <div flex="initial" present={project.due_date != null}>
              <DueDatePopover item={project} value={project.due_date} on:update={updateAttribute} on:open on:close/>
            </div>
            <div flex="initial" present={false}>
              <EstimatePopover item={project} value={project.due_date} on:update={updateAttribute} on:open on:close/>
            </div>
          </div>
        </div>
      </div>

      {#each ["assignments","tasks","documents"] as collection }
        {#if (project[collection].length > 0)}
          <div layout="row" type="list-item">
            <div flex="initial">
              <Indent level={1} width={32}/>
            </div>
            <div flex style="width: calc(100% - 24px)">
              <h4>{collection.titleize()}</h4>
              <HomeSubDocument collection={collection} bind:project={project} on:update={updateAttribute} on:refresh={refresh} {rawList}/>
            </div>
          </div>
        {/if}

      {/each}



    </section>
  {/each}
{:else}
  <section>
    <p>You currently don't have any collections yet.</p>
    <p>Go to the <a href="#/projects">Collections</a> view and assign some due dates to see some content here.</p>
  </section>
{/if}

<style lang="sass">
  section
    width: 100%
    max-width: 100%
    margin-bottom: 32px



  [type="list-item"]
    width: 100%
    max-width: 100%
    margin-bottom: 8px
    [present]
      display: none
    [present="true"]
      display: flex
    &:hover
      [present]
        display: flex

</style>
