<script lang="coffeescript">

  import { onMount } from 'svelte'

  import Router from 'svelte-spa-router'

  import { isSyncing, dueToday } from "./helpers/stores.js"
  import Pouch from "./helpers/pouch_db.js"

  # Common

  import FourOFour from './components/FourOFour.svelte'
  import LeftSplit from './components/Layout/LeftSplit.svelte'
  import Toast from "./components/Toast/Toast.svelte"
  import FavIconBadge from "./components/FavIconBadge/FavIconBadge.svelte"


  # Local


  import SideNavigator from './pages/_Common/SideNavigator.svelte'

  import Home from './pages/Home/Home.svelte'
  import Projects from './pages/Projects/Index.svelte'
  import Project from './pages/Projects/Show.svelte'
  import Document from './pages/Documents/Show.svelte'
  import Task from './pages/Tasks/Show.svelte'
  import Assignment from './pages/Assignments/Show.svelte'
  import Settings from './pages/Settings/Settings.svelte'
  import Sync from './pages/Sync/Sync.svelte'
  import Help from './pages/Help/Help.svelte'


  showToast = false
  toastMessage = ""

  routes =

    '/': Home
    '/projects': Projects
    '/projects/all': Projects
    '/projects/:id': Project
    '/projects/:project_id/tasks/:id': Task
    '/projects/:project_id/assignments/:id': Assignment
    '/projects/:project_id/documents/:id': Document



    '/settings': Settings
    '/help': Help
    '/sync': Sync
    '*': FourOFour


  appName = "Braintapper"

  pdbCallback = ()->
    console.log "window.db"
    console.log window.db
    console.log "remotedb"
    console.log window.remoteDb


  window.db = new PouchDB('braintapper', {auto_compaction: true})
  window.db.createIndex
    index:
      fields: ["type"]
  window.pouch = new Pouch window.db

  toast = (message)->
    showToast = true
    toastMessage = message

  syncCheck = ()->
    doSync = true
    if window.pouch.sync_db
      lastSyncAttempt = localStorage.getItem("last_sync_attempt")
      if lastSyncAttempt?
          minutesSinceLastSync = Date.create().minutesSince(lastSyncAttempt)
          doSync = (minutesSinceLastSync >= 5)
      unless window.pouch.is_syncing
        if doSync
          window.pouch.sync()



  syncCheck.every(60000) # minute


  onMount ()->
    window.pouch.sync_store = isSyncing
    window.pouch.notifier = toast
    window.pouch.sync()


  `$: show = showToast`
  console.log "Braintapper Application Build 1"
</script>


<svelte:head>
  <title>{ appName }</title>

</svelte:head>



<LeftSplit headerSize="40px" contentScrollable={true}>
  <div slot="nav">
    <SideNavigator/>
  </div>
  <div slot="default" style="padding: 48px 96px">
    <Router {routes}/>
  </div>
</LeftSplit>
<Toast bind:show={showToast} message={toastMessage} delay={2}/>



<FavIconBadge count={1} color="#ffffff" background="red" href="images/favicons/favicon_196_alt.png" />
