<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export is_visible = undefined

  select = (item)->
    dispatch("selected",item)


</script>
<ul role="menu">
  {#if is_visible }
  <li>
    <button type="button" on:click={select("hide")}>Hide Clip Value</button>
  </li>
  {:else}
  <li>
    <button type="button" on:click={select("show")}>Unhide Clip Value</button>
  </li>
  {/if}
  <hr/>
  <li>
    <button type="button" on:click={select("delete")}>Delete Item</button>
  </li>
</ul>
