<script lang="coffeescript">

  import PageContent from "../_Common/PageContent.svelte"
  import PageHeader from "../_Common/PageHeader.svelte"


  import FileSaver from "file-saver"
  import md5 from "md5"

  import Project from '../../helpers/project.js'


  import Checkbox from '../../components/Form/Checkbox/Checkbox.svelte'
  import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte'
  import InlinePassword from '../../components/Form/InlineInput/InlinePassword.svelte'


  import {createEventDispatcher} from "svelte"
  dispatch = createEventDispatcher()

  resource = new Project()

  replicationEnabled = localStorage.getItem("sync")
  replicationDb = localStorage.getItem("remote_db")
  replicationRequiresAuthentication = localStorage.getItem("sync_authentication") || "false"


  toggleSync = ()->
    localStorage.setItem "sync", !(replicationEnabled == 'true')
    replicationEnabled = localStorage.getItem("sync")



  updateDb = (e)->
    console.log e

  updateUser = (e)->
    console.log e

  updatePassword = (e)->
    console.log e

  saveAs = ()->

    projectLoadedScb = (projects)->
      list = projects

      jsonString = JSON.stringify(list,null,2)
      blob = new Blob([jsonString], {type:  "text/plain;charset=utf-8"})
      # note - the md5 works on the string loaded in the file, which is not the same as the file''s md5
      # use a different handler for electron
      FileSaver.saveAs blob, "braintapper-archive-#{Date.create().toTimestampId()}--#{md5(jsonString)}.json"

    resource.select(projectLoadedScb, null, null)

  forceSync = ()->
    console.log remoteDb
    if remoteDb?
      console.log "syncing to remote db"
      db.sync( remoteDb, {retry: true} )
      .on 'complete', ()->
        console.log "complete!"
      .on 'change', (change) ->
        # yo, something changed!
        console.log "sync change"
        console.log change
      .on 'paused', (info) ->
        # replication was paused, usually because of a lost connection
        console.log "sync paused"
        console.log info
      .on 'active', (info) ->
        # replication was resumed
        console.log "sync active"
        console.log info
      .on 'error', (err) ->
        console.log "sync error"
        console.log err

  importData = ()->

  restore = ()->

  destroy = ()->

  notificationPermssionRequest = ()->
    Notification.requestPermission().then (permission) ->
      # If the user accepts, let''s create a notification
      if permission == 'granted'
        notification = new Notification('Hi there!')
      else
        console.log "not granted"
      return

  if electron
    edition = "Desktop"
  else
    edition = "Web"
  `$: disableReplication = (replicationEnabled != 'true')`
</script>
<style lang="sass">
  article
    margin: 0px 52px
  button
    [disabled]
      background: var(--lightest-gray) !important
</style>

<svelte:head>
  <title>Settings</title>
</svelte:head>
<PageContent>
  <a name="top"></a>
  <PageHeader>Settings</PageHeader>
  <article type="content">


    <h2>Interface</h2>

    <div>
    Interface tweaks will be going here.
    </div>


    <h2>Data</h2>

    <h2> Warning, Items Below May Not Be Functional</h2>

    <h3>Export All Data</h3>
    <p>This will save your collections as a JSON file into your downloads folder. This does not export your settings.</p>
    <button type="button" primary on:click={saveAs}>Save to Disk</button>

    <h3>Import</h3>
    <p>Import data from a JSON file. This will create new IDs for all incoming items, so if you are importing a backup, you may end up with items having duplicate names.</p>
    <button type="button" primary on:click={importData} disabled={true}>Import</button>

    <h3>Danger Zone</h3>

    <h2>Restore</h2>
    <p>This will clear all existing data (including on any synchronized databases) and load the contents of an export file into your system. Your settings will remain intact.</p>
    <button type="button" critical on:click={restore} disabled={true}>Restore</button>

    <h2>Delete All Data</h2>
    <p>This will delete all your data and settings. If you have synchronization enabled, <b>it will delete the data from your synchronized database as well.</b></p>
    <button type="button" critical on:click={destroy}>Delete</button>


    <h2>About</h2>
    <div>Edition: {edition}</div>

  </article>

</PageContent>
