<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export item = undefined
  export value = null
  export isListItem = true

  newValue = undefined

  import { Popover } from "../../../components/Overlay"
  import { Column, ColumnCell, Row, RowCell } from "../../../components/Flex"
  import Calendar from '../../../components/Date/DatePicker/Calendar.svelte'
  import RelativeDateFormat from "../../../components/Format/RelativeDateFormat.svelte"


  # menu

  show = false

  triggerClick = ()->
    dispatch "open"
    show = true

  triggerRef = undefined

  setDate = (e)->
    newValue = e.detail
    save()
    cancel()

  cancel = ()->
    show = false
    dispatch "close"
    newValue = value

  save = ()->
    show = false
    dispatch "close"
    dispatch "update", { id: item.id, due_date: newValue }


  clear = ()->
    newValue = null
    save()

  dueDate = (due_date)->

    if due_date?
      if Object.isDate(due_date)
        return due_date
      else
        return Date.create(due_date)
    else
      return due_date

  dueDateState = (item,date)->
    if date?
      if item.percent_complete == 100
        return "complete"
      else
        if Object.isDate(date)
          return date.comparedTo(Date.create())
        else
          return Date.create(date).comparedTo(Date.create())
    else
      return "before"

  `$: newValue = value`
  `$: state = dueDateState(item,newValue)`

</script>

<style lang="sass">
  [calendar]
    padding: 0px 4px
  input
    width: 100px
    display: inline-block
  [duedate]
    &[islistitem="true"]
      margin-left: 8px
    &[islistitem="false"]
      margin-right: 8px
    &[state="before"]
      color: var(--dark-red)
    &[state="on"]
      color: var(--green)
    &[state="after"]
      color: var(--gray)
    &[state="complete"]
      color: var(--light-gray)
</style>

<div>
  <div on:click={triggerClick} bind:this={triggerRef}>

      {#if newValue == null}
        {#if isListItem}
          <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M22 3h-3V1h-2v2H7V1H5v2H2v20h20V3zm-2 18H4V8h16v13z" fill="var(--gray)"/></svg>
        {:else}
          <a duedate {state} {isListItem}><em>No Due Date specified</em></a>
        {/if}
      {:else}
        <a duedate {state} {isListItem}><em>
          {#if !isListItem}
            <span duedate {state}>Due </span>
          {/if}
          <RelativeDateFormat value={dueDate(newValue)}/>

        </em></a>
      {/if}

  </div>
  <Popover bind:show={show} on:close={cancel} bind:trigger={triggerRef} width="240px" align="right">
    <Column>
      <ColumnCell>
        <Row>
          <RowCell flex="flex" style="padding: 4px 0px 4px 8px">
            {#if newValue == null}
              No Due Date
            {:else}
              <RelativeDateFormat value={dueDate(newValue)}/>
            {/if}
          </RowCell>
          <RowCell>
            <button type="button" icon on:click={clear}>
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z" class="fill"/></svg>
            </button>
          </RowCell>
        </Row>
      </ColumnCell>
      <ColumnCell>
        <div calendar>
          <Calendar bind:value={newValue} on:change={setDate}/>
        </div>
      </ColumnCell>
    </Column>
  </Popover>
</div>
