var Clip, SubDocument;

SubDocument = require("./sub_document.js");

Clip = (function() {
  class Clip extends SubDocument {
    model() {
      return {
        // if sourceversion < latest version, then some upgrades may be required
        // if null then return latest
        id: null,
        type: this.type,
        name: null,
        is_favorite: false,
        ordinal: null,
        indent: 0,
        clip_count: 0,
        click_count: 0,
        content: "",
        content_indexable: "",
        is_visible: true,
        assigned_to: null,
        created_at: null,
        updated_at: null,
        metadata: {}
      };
    }

  };

  Clip.prototype.name = "Clip";

  Clip.prototype.type = "clip";

  return Clip;

}).call(this);

module.exports = Clip;
