<!-- based on CLEditor by https://github.com/nenadpnc -->

<script lang="coffeescript">



  export actions = []
  export height = "300px"
  export html = ""
  export useMarkdown = false
  export content = undefined

  export id = "viewer-#{uuid()}"
  export placeholder = "This document is currently empty. Edit it to add content."



  setContent = (content)->
    if (content || "").trim().compact().length == 0
      return "<div class='placeholder' style='color: var(--gray);'>#{placeholder}</div>"
    else
      return content


</script>

<style lang="sass">


</style>

<div on:dblclick>
  <article class="ql-container ql-bubble"><div class="ql-editor">{@html setContent(content)}</div></article>
</div>
