<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import tippy from 'tippy.js'


  export value = Date.create()
  export style = undefined

  import DueDate from "./_DueDate.svelte"
  import RelativeDateFormat from "../../../components/Format/RelativeDateFormat.svelte"

  tippyObject = undefined
  component = undefined



  export item = undefined

  export isListItem = true

  newValue = undefined

  import { Popover } from "../../../components/Overlay"
  import { Column, ColumnCell, Row, RowCell } from "../../../components/Flex"
  import Calendar from '../../../components/Date/DatePicker/Calendar.svelte'







  popover = (element, content) ->

    tippyOptions =
      theme: "datepicker"
      trigger: "click"
      placement: "bottom-end"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$set { dateValue: newValue }
        component.$on "clear",  (e) ->
          tippyObject.hide()
          console.error "clear"
          newValue = null
          dispatch "close"
          dispatch "update", { id: item.id, due_date: newValue }
          component.$set { dateValue: newValue }

        component.$on "set-date",  (e) ->
          console.error "set-date"
          value = e.detail
          console.log e.detail
          newValue = e.detail.format("{yyyy}-{MM}-{dd}")
          component.$set { dateValue: newValue }
          dispatch "update", { id: item.id, due_date: newValue }
          
          tippyObject.hide()
        return

    else
      tippyOptions.content = content
    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        console.error "update"
        if component
          component.$set { dateValue: newValue }

        return
      destroy: ->
        tippyObject.destroy()
        if component
          # debugger
          component.$destroy()
        return

    }



  dueDateState = (item,date)->
    if date?
      if item.percent_complete == 100
        return "complete"
      else
        if Object.isDate(date)
          return date.comparedTo(Date.create())
        else
          return Date.create(date).comparedTo(Date.create())
    else
      return "before"

  `$: newValue = value`
  `$: state = dueDateState(item,newValue)`


</script>

<style lang="sass">
  [calendar]
    padding: 0px 4px
  input
    width: 100px
    display: inline-block
  [duedate]
    &[islistitem="true"]
      margin-left: 8px
    &[islistitem="false"]
      margin-right: 8px
    &[state="before"]
      color: var(--dark-red)
    &[state="on"]
      color: var(--green)
    &[state="after"]
      color: var(--gray)
    &[state="complete"]
      color: var(--light-gray)
</style>

<div use:popover={DueDate}>

    {#if newValue == null}
      {#if isListItem}
        <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M22 3h-3V1h-2v2H7V1H5v2H2v20h20V3zm-2 18H4V8h16v13z" fill="var(--gray)"/></svg>
      {:else}
        <a duedate {state} {isListItem}><em>No Due Date specified</em></a>
      {/if}
    {:else}
      <a duedate {state} {isListItem}><em>
        {#if !isListItem}
          <span duedate {state}>Due </span>
        {/if}
        <RelativeDateFormat value={newValue}/>
      </em></a>
    {/if}

</div>
