<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export item = undefined
  export value = 0

  newValue = undefined



  import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte'
  import Gravatar from '../../../components/External/Gravatar/Gravatar.svelte'

  # menu

  show = false

  triggerClick = ()->
    dispatch "open"
    show = true


  triggerRef = undefined
  progressMenuClick = (e)->
    console.log "progressmenuclick"


  cancel = ()->
    show = false
    dispatch "close"
    newValue = value

  save = ()->
    show = false
    dispatch "close"
    dispatch "save", { id: item.id, assigned_to_id: newValue }


  markComplete = ()->
    newValue = 100

  clear = ()->
    newValue = null

  switch item.assigned_to_id
    when null
      state = "empty"
    else
      state = "green"

    #}

  `$: newValue = value || 0`

</script>
<style lang="sass">

  input
    width: 100px
    display: inline-block
  svg
    &[state]
      &:hover
        .fill
          fill: var(--gray)
      .fill
        fill: var(--green)
    &[state="empty"]
      &:hover
        .fill
          fill: var(--blue)
      .fill
        fill: var(--lightest-gray)

</style>

