<script lang="coffeescript">

  import { createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()

  import Editor from "./Quill.svelte"
  import Viewer from "./Viewer.svelte"


  export height = "100%"
  export placeholder = "Type in some content here"

  export content = undefined
  export text = undefined

  export markdown = undefined



  editorContent = undefined
  viewerContent = undefined

  saveObject = undefined




  isEditing = false


  edit = (e)->
    isEditing = true

  changeContent = (e)->
    saveObject = e.detail

  debouncedChange = changeContent.debounce(500)


  saveAndCancel = ()->
    dispatch "save", saveObject
    isEditing = false

  `$: viewerContent = editorContent = content`

</script>

{#if isEditing}
  <div class="togglable-notice" {isEditing} on:click={saveAndCancel}>Editing....Click outside the editor to save changes.</div>
  <div class="togglable-editor" {isEditing}>
    <Editor content={editorContent} on:change={changeContent} on:blur={saveAndCancel} {placeholder}/>
  </div>
{:else}
  <div class="togglable-editor" {isEditing}>
    <Viewer content={viewerContent}  on:dblclick={edit} placeholder="This has no description at the moment. Double click to edit"/>
  </div>
{/if}

