<script lang="coffeescript">


  import { createEventDispatcher, onMount, afterUpdate } from 'svelte'
  import { slide } from 'svelte/transition'


  dispatch = createEventDispatcher()

  export disabled = false
  export id = uuid()

  export width = '200px'

  export selectedIndex = -1
  export size = undefined


  export value = ""
  export blankSlate = ""

  export labelKey = "label"
  export idKey = "id"


  export show = false

  export align = "left"

  # trigger is required
  export trigger = undefined



  style = undefined

  menuRef = undefined

  hasfocus = false

  highlightedIndex = -1

  changeIndex = (direction) ->
    index = highlightedIndex + direction;

    if (index < 0)
      index = items.length - 1
    else if (index >= items.length)
      index = 0
    highlightedIndex = index



  menuId = uuid()

  onMount ()->



  checkTrigger = (i)->

    if i?

      if align == "left"
        style = "width: #{width};top: #{i.offsetParent.clientHeight + 4}px;"
      else
        if i.offsetParent?
          # console.log "offsetparent"
          triggerContainerWidth = i.offsetParent.clientWidth
          triggerCoords = i.getBoundingClientRect()
          right = triggerContainerWidth  - (triggerCoords.width)
          style = "width: #{width};top: #{i.offsetParent.clientHeight + 4}px; right: #{right}px"

        else
          # console.log "#{Date.create()} nooffsetparent"
          # console.log i
          # console.log u(i)

          triggerContainerWidth = i.clientWidth
          triggerCoords = i.getBoundingClientRect()
          right = triggerContainerWidth  - (triggerCoords.width)
          style = "width: #{width};top: #{i.clientHeight + 4}px; right: #{right}px"

  `$: checkTrigger(trigger)`



  overlayClick = ()->
    `show = false`
    dispatch "close"

  noop = (e)->
    console.log e



</script>


<style lang="sass">


  div[menu]
    position: absolute

    background: white
    display: inline-block
    border: 1px solid var(--menu-border)
    text-align: left
    z-index: var(--z-index-menu)
    &[align="left"]
      left: 0


  [menu-item]
    display: block
    position: relative
    width: 100%
    padding: var(--fine-spacing-s) var(--fine-spacing-m)
    &:hover
      background: var(--lighter-gray)



</style>
<div type="overlay" clear open={show} on:click={overlayClick} draggable="false"/>
{#if show}
  <div bind:this={menuRef} menu id={menuId} transition:slide="{{duration: 75}}" {style} {align} on:dragstart|preventDefault|stopPropagation={noop}>
    <slot></slot>
  </div>
{/if}
