<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export item = undefined
  export value = 0

  newValue = undefined

  import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte'
  import Gravatar from '../../../components/External/Gravatar/Gravatar.svelte'

  # menu

  show = false

  triggerClick = ()->
    dispatch "open"
    show = true


  triggerRef = undefined
  progressMenuClick = (e)->
    console.log "progressmenuclick"


  cancel = ()->
    show = false
    dispatch "close"
    newValue = value

  save = ()->
    show = false
    dispatch "close"
    dispatch "save", { id: item.id, assigned_to_id: newValue }


  markComplete = ()->
    newValue = 100

  clear = ()->
    newValue = null

  switch item.assigned_to_id
    when null
      state = "empty"
    else
      state = "green"

    #}

  `$: newValue = value || 0`

</script>
<style lang="sass">

  input
    width: 100px
    display: inline-block
  svg
    &[state]
      &:hover
        .fill
          fill: var(--gray)
      .fill
        fill: var(--green)
    &[state="empty"]
      &:hover
        .fill
          fill: var(--blue)
      .fill
        fill: var(--lightest-gray)

</style>

<div>
  <Tooltip content={item.checklists.length}>
    {#if state == "empty"}
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M19 5v14H5V5h14m2-2H3v18h18V3z" class="fill"/></svg>
    {:else if state=="partial"}
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M21 3H3v18h18V3zM10 17l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z" class="fill"/></svg>
    {:else}
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M18 7l-1.41-1.41-6.34 6.34 1.41 1.41L18 7zm4.24-1.41L11.66 16.17 7.48 12l-1.41 1.41L11.66 19l12-12-1.42-1.41zM.41 13.41L6 19l1.41-1.41L1.83 12 .41 13.41z" class="fill"/></svg>
    {/if}
  </Tooltip>
</div>
