<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()


  export list = []

  select = (item)->
    dispatch("selected",item)

  sort = (list)->
    if list?
      return list.sortBy "ordinal"
    else
      []
  `$: sortedlist = sort(list)`
</script>
<ul role="menu">
  <li>
    <button type="button" on:click={select("collections")}>Collections</button>
  </li>

  {#if list}
  <hr/>
  {#each sortedlist as item, index}
  <li>
    <button type="button" on:click={select(item.id)} ellipsis><span ellipsis>{item.name}</span></button>
  </li>
  {/each}
  {/if}
</ul>
