<script lang="coffeescript">
  import { createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()


  import tippy from 'tippy.js'


  export item = undefined

  # If list item, show an icon when there is no value
  # If not a list item, show text instead
  export isListItem = true

  # Show time entry quick add
  # Only show on items that support time entries
  # tasks, assignments, documents



  newValue = undefined

  import Menu from "./_Time.svelte"

  import EffortFormat from '../../../components/Format/EffortFormat.svelte'

  # menu

  show = false
  stub = {
    estimate: 0
  }


  effort = 0




  # --- tippy


  handleMenuSelection = (selection)->
    switch selection
      when "delete"
        showDeleteConfirmation = true
      else
        console.log "do nothing"


  popover = (element, content) ->
    tippyOptions =
      distance: '-20px'
      theme: "menu"
      appendTo: 'parent'
      trigger: "click"
      placement: "bottom-end"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$set { effort: (item.estimate / 60) }


        component.$on "save",  (e) ->
          tippyObject.hide()
          progressItem = { id: item.id, estimate: e.detail }
          dispatch "close"
          dispatch "update", progressItem

        component.$on "cancel",  (e) ->
          console.log "cancel"
          tippyObject.hide()

        return

    else
      tippyOptions.content = content
    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        console.error "update"
        #if component
        #  component.$set { value: selectedDate }

        return
      destroy: ->
        tippyObject.destroy()
        if component
          # debugger
          component.$destroy()
        return

    }






  # --- /tippy

















  onMount ()->

    effort = item.estimate / 60

  triggerClick = ()->
    console.log "trigger click"
    dispatch "open"
    show = true


  triggerRef = undefined
  progressMenuClick = (e)->
    console.log "progressmenuclick"




  cancel = ()->
    show = false
    dispatch "close"


  save = ()->
    # show = false
    progressItem = { id: item.id, estimate: stub.estimate }


    dispatch "close"
    dispatch "update", progressItem


  add = ()->
    # show = false
    progressItem = { estimated_hours: newValue }


    dispatch "close"
    dispatch "save", progressItem




  newTime = 0








</script>

<div>

  <div use:popover={Menu}>
      {#if (item.estimate || 0) == 0}
        {#if isListItem}
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
            <path fill="none" d="M0 0h24v24H0V0z"/>
            <path d="M11.99 2C6.47 2 2 6.48 2 12s4.47 10 9.99 10C17.52 22 22 17.52 22 12S17.52 2 11.99 2zM12 20c-4.42 0-8-3.58-8-8s3.58-8 8-8 8 3.58 8 8-3.58 8-8 8zm.5-13H11v6l5.25 3.15.75-1.23-4.5-2.67z" class="fill"/>
          </svg>
        {:else}
          <a duedate {isListItem} style="color: var(--gray)"><em>No estimate</em></a>
        {/if}

      {:else}
        <a duedate style="color: var(--gray)"><em>
          {#if !isListItem}
            <span duedate>Estimate: </span>
          {/if}
          <EffortFormat bind:value={item.estimate}/>
        </em></a>

      {/if}


  </div>

</div>
