<script lang="coffeescript">


  import { Row, RowCell } from "../../../../components/Flex"
  import Menu from '../../../../components/Overlay/Menu/ComposedMenu.svelte'

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export index = undefined
  export item = undefined
  export listId = undefined
  export indentable = true
  export sameListOnly = false
  export showMenu = true


  showIndentMenu = false

  indentButtonRef = undefined

  draggableEl = undefined
  draggable = false

  isOver = false
  isInvalid = false

  checkTypesForListId = (types)->
    types.find (t)->
      return t.startsWith("listid:")

  start = (e)->
    item["listId"] = listId
    item["index"] = index || null
    e.dataTransfer.setData("text/plain", JSON.stringify(item))
    e.dataTransfer.setData("listid:#{listId}", JSON.stringify(item))



  over = (e)->
    e.preventDefault()
    isOver = true
    unless !sameListOnly
      draggedListId = checkTypesForListId(e.dataTransfer.types)
      if draggedListId?
        isInvalid = !(listId == draggedListId.replace("listid:",""))
      else
        isInvalid = true

  leave = (e)->
    draggable = false
    isOver = false
    isInvalid = false

  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false
    e.preventDefault()
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))
    dropped = true
    unless !sameListOnly
      # console.log "This list: #{listId} :: itemList #{dragItem.listId}"
      dropped = (listId == dragItem.listId)
    if dropped
      dispatch "drop",
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: item
        dropIndex: index
        dropListId: listId

  enter = (e)->
    console.log "etnered"
    console.log e

  mouseup = ()->
    draggable = false
    isInvalid = false

  mousedown = ()->
    draggable = true

  getWidth = (el)->
    if el?
      el.clientWidth - 48
    else
      0
  closeMenu = ()->
    showIndentMenu = false

  indent = (indentLevel)->
    console.log "indent #{indentLevel}"
    dispatch "update", {id: item.id, indent: indentLevel }
    # console.log {id: item.id, indent: indentLevel }
    closeMenu()

  toggleIndentMenu = ()->
    showIndentMenu = true

  `$: draggableWidth = getWidth(draggableEl) `

</script>


<div
  item-draggable
  data-id={item.id}
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragover={over}
  on:dragstart={start}
  on:dragleave={leave}
  bind:this={draggableEl}
  {indentable}

>

      <div flex="initial" handle on:mouseup={mouseup(item)} on:mousedown={mousedown(item)}>
        <slot name="grabber">
          <svg class="handle" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M9,3H11V5H9V3M13,3H15V5H13V3M9,7H11V9H9V7M13,7H15V9H13V7M9,11H11V13H9V11M13,11H15V13H13V11M9,15H11V17H9V15M13,15H15V17H13V15M9,19H11V21H9V19M13,19H15V21H13V19Z"/></svg>
        </slot>
      </div>
      <div flex="initial" indent-menu>
        <button bind:this={indentButtonRef} icon on:click={toggleIndentMenu}>
          <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" version="1.1" viewBox="0 0 24 24"><path d="M20,18H22V6H20M11.59,7.41L15.17,11H1V13H15.17L11.59,16.58L13,18L19,12L13,6L11.59,7.41Z"/></svg>
        </button>
        <Menu trigger={indentButtonRef} bind:showMenu={showIndentMenu} align="left" width="189px">
          <ul>
            <li>
              {#each [0,1,2,3,4,5] as indentLevel}
                <button type="button" on:click={indent(indentLevel)} disabled={(item.indent || 0) == indentLevel} indent>{indentLevel}</button>
              {/each}
            </li>
          </ul>
        </Menu>
      </div>
      <div flex class="slot"><slot item={item} index={index}/></div>
      <div flex="initial" draggable-item-menu style="padding-left: 24px;padding-right: 4px">
        <slot name="menu"/>
      </div>

</div>
<style global lang="sass">
  // global to ensure child menus inherit styles
  [item-draggable]

    border-radius: 3px
    &[draggable="true"]


    box-sizing: border-box
    display: flex

    padding: 4px 0px

    flex-direction: row

    width: calc(100% + 56px)
    max-width: calc(100% + 56px)

    .slot
      width: calc(100% - 96px)
      max-width: calc(100% - 96px)

    margin-left: -24px
    &[indentable="true"]
      width: calc(100% + 104px)
      max-width: calc(100% + 104px)
      margin-left: -52px

    [handle], [draggable-item-menu], [indent-menu]
      width: 24px
      svg
        display: none

    [handle]:hover
      cursor: grab
    &.over
      background: #dddddd
    &.invalid
      background: var(--dark-red) !important
    &:hover
      background: #fcfcfc
      [handle], [indent-menu], [draggable-item-menu]
        svg, svg.menu
          display: inline

      [handle]
        svg
          path
            fill: var(--light-gray)
      [draggable-item-menu]
        svg
          path
            fill: var(--dark-gray)
        &:hover
          svg
            path
              fill: var(--blue)

      [indent-menu]
        button[icon]
          padding: 3px 0px
        svg
          path
            fill: var(--light-gray)
        &:hover
          svg
            path
              fill: var(--blue)


    & > [flex]
      flex: 1
      box-sizing: border-box
      max-height: 100%


    & > [flex=initial]
      flex: 0 1 auto

    & > [flex=auto]
      flex: 1 1 auto

    & > [flex=none]
      flex: 0 0 auto


    & > [flex=noshrink]
      flex: 1 0 auto


    & > [flex=nogrow]
      flex: 0 1 auto


    ul
      width: 100%
      li
        min-height: 32px
        div
          padding: 6px 12px
        button
          display: inline
          border-radius: 0px
          width: 100%

          text-align: left
          &[indent]
            background: transparent
            &:first-of-type
              margin-left: 2px
            border-radius: 4px
            width: auto
            font-weight: bold
          &[disabled]
            color: var(--light-gray)
          &:hover
            background: var(--lightest-gray)


</style>
