<script lang="coffeescript">
  # todo: implement cancel
  import { tick, createEventDispatcher, onMount } from 'svelte'

  import DurationInput from "../TranslatedInput/DurationInput.svelte"

  dispatch = createEventDispatcher()

  # todo: cancel

  # Props
  export value = ''
  export style = undefined
  export inputStyle = undefined
  export type = 'text'
  export placeholder = ''
  export labelClasses = ''
  export inputClasses = ''

  export doubleClick = true

  export id = undefined
  export delay = 250


  export editing = false
  inputEl = undefined
  label = undefined
  originalValue = undefined


  # Computed
  `$: isText = type === 'text'`
  `$: isNumber = type === 'number'`
  `$: if (isNumber) {
        label = value === '' ? placeholder : value;
      } else if (isText) {
        label = value ? value : placeholder;
      }
  const toggle = async (_) => {
    editing = !editing;
    originalValue = value;
    if (editing) {
      await tick();
      inputEl.focus();
    }
  }`

  onMount ()->
    originalValue = value
  handleInput = (e) ->
    if isNumber
      value = +e.target.value
    else
    value = e.target.value


  handleKeyup = (e) ->
    # dispatch("keyup", value)

    switch e.keyCode
      when 13
        # inputEl.blur()
        console.log "should save"
        editing = false
        dispatch "update", value
      when 27
        value = originalValue
        editing = false
        console.log "should cancel"
    # }

  handleBlur = (_) ->
    unchanged = (value == originalValue)
    # toggle()
    editing = false
    unless unchanged
      dispatch("update",value)


  # Click tracking

  clickCount = 0

  clickReset = ()->
    switch clickCount
      when 1
        single()
      when 2
        double()
      else
        clickCount = 0
    # }
  delayedReset = clickReset.debounce(delay)

  single = () ->
    clickCount = 0
    if !doubleClick
      toggle()
    dispatch "clicked", id

  double = () ->
    clickCount = 0
    if doubleClick
      toggle()
    dispatch "doubleclicked", id

  click = ()->
    clickCount++
    delayedReset()

  handleInputClick = (e)->
    e.preventDefault

  handleChange = (e)->
    console.log "handleChange"
    value = e.detail.inMinutes

</script>
<style lang="sass">

  input, div, div span

    font-family: inherit
    font-size: inherit
    font-weight: inherit
    border: 0px solid transparent
    padding: 0px
  input
    color: var(--dark-red)
  div
    // padding: var(--fine-spacing-xs) var(--fine-spacing-m)
    min-height: var(--font-xl)
    border: 1px solid transparent



</style>
{#if editing && (isText || isNumber)}
  <DurationInput
    value={value / 60}
    isMinutes={true}
    on:change={handleChange}
    on:input={handleInput}
    on:keyup={handleKeyup}
    on:blur={handleBlur}
    on:click={handleInputClick}
    />

{:else}
  <div
    {id}
    class={labelClasses}
    ellipsis {style}>
    <span on:click={click} ellipsis><slot {label}><span ellipsis>{label}<span spacer/></span></slot></span>
  </div>
{/if}
