<script lang="coffeescript">


  # todo: integrate this into a core lib?
  import HumanEffort from "../../helpers/human_effort.js"


  export value = undefined
  export units = "minutes" # input unit
  export dayHours = 8 # hours per day
  export aggregate = true # return as raw or not



  effortInput = HumanEffort.humanizeHours(value || 0)

  change = (e)->
    event = {
      inputValue: value
      inHours: HumanEffort.parseHours(value || "")
      inMinutes: HumanEffort.parseHours (value || ""), true
    }
    console.log event
    dispatch "change", event
  debouncedChange = change.debounce(500)

  translate = (val)->
    hours = HumanEffort.parseHours(value || "")
    console.log "Hours: #{hours} Units: #{units}"
    switch units
      when "hours"
        hours = HumanEffort.parseHours(value || "")
      else # "minutes"
        hours = HumanEffort.parseHours(value / 60|| "")


    if aggregate
      if hours < 1
        return "#{(hours * 60).format()}m"
      else if hours > dayHours
        return "#{(hours / dayHours).format()}d"
      else
        return "#{(hours).format()}h"


    else
      return hours








</script>
{translate(value)}
