<script lang="coffeescript">

  export value = undefined
  export includeTime = true

  # todo: show time will change conditions

  translate = (val)->

    # comparator dates
    baseDate = Date.create(val).beginningOfDay()
    yesterday = baseDate.clone().addDays(-1)
    tomorrow = baseDate.clone().addDays(1)




    theDate = Date.create(val)

    # check if past or future
    # if past - "ago"
    # if future - "from now"


    dateFormat = "%Y-%m-%d"
    timeFormat = "%H:%M"

    isFuture = theDate.isFuture()

    if isFuture
      prefix = "last"
      suffix = "from now"
    else
      prefix = "next"
      suffix = "ago"

    isPast = !isFuture


    ###
      Rules
        PAST before: 00:00
          Yesterday "Yesterday [at hh:mm]"
          Up to a week ago - "days ago (mm/dd) [at hh:mm]"
          More than a week ago - "Full date [at hh:mm]"
        TODAY - since: 00 until 23:59
          pastPrefix = "ago"
          futurePrefix = "from now"
          < 1m "moments [prefix]"
          < 60m "minutes [prefix]"
          < 4h "hours [prefix]"
          < "[at hh:mm]"
        FUTURE after 23:59
          Tomorrow "Tomorrow [at hh:mm]"
          Up to a week from now - "Next full-day-of-week [at hh:mm]"
          More than a week from now - "Full date [at hh:mm]"
    ###



    if Math.abs(theDate.secondsFromNow()) < 60
      "moments #{suffix}"
    else if Math.abs(theDate.minutesFromNow()) < 60
      "#{Math.abs(theDate.hoursFromNow())} #{"minutes".pluralize(Math.abs(theDate.minutesFromNow()))} #{suffix}"
    else if Math.abs(theDate.hoursFromNow()) < 24
      "#{Math.abs(theDate.hoursFromNow())} #{"hours".pluralize(Math.abs(theDate.minutesFromNow()))} #{suffix}"
    else if Math.abs(theDate.daysFromNow()) < 7
      if Math.abs(theDate.daysFromNow()) == 1
        if isPast
          "Yesterday at #{theDate.format(timeFormat)}"
        else
          "Tomorrow at #{theDate.format(timeFormat)}"
      else if Math.abs(theDate.daysFromNow()) < 7
        if isFuture
          prefix = ""
        "#{prefix.capitalize()} #{theDate.format("%A at " + timeFormat)}".compact()

      else
        "#{theDate.format()}"
    else
      "#{theDate.format()}"








  `$: output = translate(value)`
</script>

{output}
