<script lang="coffeescript">

  import { createEventDispatcher } from 'svelte';

  dispatch = createEventDispatcher()

  import Modal from "./ModalGeneric.svelte"

  export entity = "item"

  destroy = ()->
    dispatch "destroy"
  cancel = ()->
    dispatch "cancel"

</script>


<Modal>
  <div slot="header">Delete this {entity}?</div>
  <div slot="footer">
    <div a="r">
      <button on:click="{cancel}" type="button" normal >Cancel</button>
      <button on:click="{destroy}" type="button" normal critical margin-left="s">Delete</button>
    </div>
  </div>
  <div>
    Are you sure you want to do this?
  </div>
</Modal>
