<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()



  import tippy from 'tippy.js'

  import {push} from 'svelte-spa-router'


  import Menu from "./_Favorites.svelte"
  import Project from "../../../helpers/project.js"



  tippyObject = undefined
  component = undefined


  resource = new Project()
  list = []
  getProjects = ()->
    scb = (projects)->
      component.$set { list: projects }
      console.log "scb"
      console.log projects
    resource.select(scb, null, null)

  getProjects()



  handleMenuSelection = (selection)->
    switch selection
      when "collections"
        push "#/projects"
      else
        push "#/projects/#{selection}"
    # "


  popover = (element, content) ->
    tippyOptions =
      distance: '0px'
      theme: "menu"
      appendTo: 'parent'
      trigger: 'click'
      placement: "bottom-end"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$on "selected",  (e) ->
          tippyObject.hide()
          handleMenuSelection e.detail

        component.$set { list: list }
        return

    else
      tippyOptions.content = content
    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        console.error "update"
        if component
          component.$set { list: list }

        return
      destroy: ->
        tippyObject.destroy()
        if component
          # debugger
          component.$destroy()
        return

    }






  width = "200px"

</script>
<style lang="sass">

  a

    em
      color: var(--black)
      font-style: normal
      font-weight: var(--font-bold)

</style>

<a name="collections" use:popover={Menu}><em>Collections</em></a>
