<script lang="coffeescript">

  import {createEventDispatcher} from "svelte"

  import DurationInput from "../../../components/Form/TranslatedInput/DurationInput.svelte"
  import DatePicker from "../../../components/Date/DatePicker/DatePicker.svelte"

  import AddStub from "./_AddStub.svelte"

  export stubPlaceholder = undefined

  itemType = "time entry"

  export showForm = false

  effort = 0

  stub = {
    booking_date: Date.create()
    effort_minutes: 0
  }

  resetStub = ()->
    stub.effort_minutes =  0
    effort = 0
    console.log "resetStub"
    console.log stub

  edit = ()->
    showForm = true

  cancel = ()->
    resetStub()
    showForm = false

  console.log

  dispatch = createEventDispatcher()

  add = ()->
    console.log "enter"
    console.log stub
    if stub.effort_minutes > 0
      dispatch "add", stub
      resetStub()

  datepickerKeyup = (e)->
    if (e.key == "Escape")
      cancel()

  durationKeyup = (e)->
    switch e.key
      when "Escape"
        cancel()
      when "Enter"
        add()
    #}


  bookingDate = Date.create()

  updateDate = (e)->
    console.warn "updateDate"
    console.log "bookingDcate: #{bookingDate}"
    stub.booking_date = Date.create(bookingDate)

  updateEffortMinutes = (e)->
    console.log "updateEffortMinutes"
    console.log e.detail
    stub.effort_minutes = e.detail.inMinutes

</script>



{#if !showForm}
  <AddStub {itemType} on:edit={edit} {stubPlaceholder}/>
{:else}
  <div layout="row">

    <div flex="initial">
      <DatePicker bind:value={bookingDate} on:change={updateDate} on:keyup={datepickerKeyup}/>
    </div>

    <div flex>
      <DurationInput bind:value={effort} on:change={updateEffortMinutes} autofocus on:keyup={durationKeyup}/>
    </div>

    <div flex="initial">
      <button type="button" icon on:click={add} disabled={false}>
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 512 512"><title>ionicons-v5-a</title><line x1="256" y1="112" x2="256" y2="400" style="fill:none;stroke:#000;stroke-linecap:round;stroke-linejoin:round;stroke-width:32px"/><line x1="400" y1="256" x2="112" y2="256" style="fill:none;stroke:#000;stroke-linecap:round;stroke-linejoin:round;stroke-width:32px"/></svg>
      </button>
    </div>
    <div flex="initial">
      <button type="button" icon on:click={cancel}>
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"/></svg>
      </button>
    </div>
  </div>
{/if}
