// requires global variable db, declared in App.svelte
var PouchResource;

PouchResource = (function() {
  class PouchResource {
    model() {
      return {
        // if sourceversion < latest version, then some upgrades may be required
        // if null then return latest
        _id: null,
        id: null,
        type: this.type,
        name: null,
        due_date: null,
        completion_date: null,
        estimate: null,
        progress: {
          percent_complete: 0,
          color: "light-gray"
        },
        ordinal: null,
        indent: 0,
        hierarchy: null,
        content: "",
        content_indexable: "",
        assigned_to: [],
        assignments: [],
        attachments: [],
        tasks: [],
        documents: [],
        clips: [],
        attachments: [],
        created_at: null,
        updated_at: null,
        metadata: {},
        is_favorite: false,
        is_archived: false,
        is_locked: false,
        is_template: false,
        last_export: null,
        settings: {
          hours_per_day: 8,
          sections: {
            about: {
              enabled: true,
              expanded: true,
              label: "Description"
            },
            assignments: {
              enabled: true,
              expanded: true,
              label: "Assignments"
            },
            tasks: {
              enabled: true,
              expanded: true,
              label: "Tasks"
            },
            documents: {
              enabled: true,
              expanded: true,
              label: "Documents"
            },
            clips: {
              enabled: true,
              expanded: true,
              label: "Clips"
            },
            attachments: {
              enabled: true,
              expanded: true,
              label: "Assignments"
            },
            settings: {
              enabled: true,
              expanded: true,
              label: "Settings"
            }
          }
        }
      };
    }

    upgradeModel(item) {
      var latest;
      // check version
      latest = this.model();
      //console.log item
      delete item.description;
      if (item.progress == null) {
        item["progress"] = {
          percent_complete: item.percent_complete,
          color: "gray"
        };
        delete item.percent_complete;
      }
      if (item.settings.sections == null) {
        item.settings["sections"] = latest.settings.sections; // get rid of obsolete
      } else {
        if (item.settings.sections.description != null) {
          item.settings.sections["about"] = item.settings.sections.description;
          delete item.settings.sections.description;
        }
        if (item.settings.sections.details != null) {
          delete item.settings.sections.details;
        }
      }
      return item;
    }

    stub(override) {
      var base, newId;
      base = this.model();
      newId = uuid();
      base._id = newId;
      base.id = newId;
      base.name = `New ${this.type} created at ${Date.create().format('%Y-%m-%d %H:%M:%S')}`;
      base.created_at = Date.create();
      base.updated_at = Date.create();
      if (override != null) {
        return Object.merge(base, override, true);
      } else {
        return base;
      }
    }

    transformCollection(items) {
      var that;
      that = this;
      //console.log items
      return items.map(function(item) {
        return that.transformModel(item);
      });
    }

    transformModel(item) {
      var base, upgraded;
      base = this.model();
      base.id = item._id;
      upgraded = Object.merge(base, this.upgradeModel(item), true);
      //console.log upgraded
      return upgraded;
    }

    constructor(options) {
      var that;
      that = this;
      this.after_construct();
    }

    after_construct() {}

    sync() {
      console.log("pouchresourece.sync");
      return window.pouch.sync();
    }

    select(thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.find({
        selector: {
          type: this.type
        }
      }).then(function(result) {
        if (thenCb != null) {
          return thenCb(that.transformCollection(result.docs));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    selectOne(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      // console.log "selectone #{id}"
      return db.get(id).then(function(doc) {
        //console.log "select one"
        //console.log doc
        if (thenCb != null) {
          return thenCb(that.transformModel(doc));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select on e err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    find(options, thenCb, catchCb, finallyCb) {
      return db.find(options).then(function(result) {
        if (thenCb != null) {
          return thenCb(that.transformCollection(result.docs));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    transformBeforeSave(document) {
      return document;
    }

    insert(obj, thenCb, catchCb, finallyCb) {
      var newRecord, that;
      that = this;
      newRecord = this.stub(obj);
      //console.log "newRecord"
      //console.log newRecord
      newRecord.created_at = Date.create();
      newRecord = this.transformBeforeSave(newRecord);
      return db.put(newRecord).then(function(response) {
        // note the response is this format:
        // ok: true
        // id: id
        // rev: rev_id
        if (thenCb != null) {
          return thenCb(response);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("insert.catch");
          return console.log(err);
        }
      }).finally(function() {
        console.log("insert finally");
        that.sync();
        if (finallyCb != null) {
          console.log("insert.finally");
          return finallyCb();
        }
      });
    }

    update(changedDoc, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      //console.log "updateDocument"
      //console.log changedDoc
      //console.log "attempt to update #{changedDoc.id}"
      return db.get(changedDoc.id).then(function(originalDoc) {
        changedDoc["_rev"] = originalDoc._rev;
        changedDoc.updated_at = Date.create();
        changedDoc = that.transformBeforeSave(changedDoc);
        if (thenCb != null) {
          return thenCb(db.put(changedDoc));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("update.catch");
          return console.log(err);
        }
      }).finally(function() {
        console.log("update finally");
        that.sync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    updateAttributes(changedDoc, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.get(changedDoc.id).then(function(originalDoc) {
        Object.merge(originalDoc, changedDoc);
        originalDoc.updated_at = Date.create();
        if (thenCb != null) {
          return thenCb(db.put(originalDoc));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("update.catch");
          return console.log(err);
        }
      }).finally(function() {
        that.sync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    destroy(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.get(id).then(function(response) {
        if (thenCb != null) {
          return thenCb(db.remove(response));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("destroy.catch");
          return console.log(err);
        }
      }).finally(function() {
        console.log("destroy finally");
        that.sync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

  };

  PouchResource.prototype.name = "Resource Name";

  PouchResource.prototype.type = "resource";

  PouchResource.prototype.version = 1;

  return PouchResource;

}).call(this);

module.exports = PouchResource;
