var ChecklistItem, SubDocument;

SubDocument = require("./sub_document.js");

ChecklistItem = (function() {
  class ChecklistItem extends SubDocument {
    model() {
      return {
        // if sourceversion < latest version, then some upgrades may be required
        // if null then return latest
        id: null,
        type: this.type,
        ordinal: null,
        indent: 0,
        content: "",
        content_indexable: "",
        is_completed: false,
        assigned_to: [],
        created_at: null,
        updated_at: null,
        metadata: {}
      };
    }

  };

  ChecklistItem.prototype.name = "Checklist Item";

  ChecklistItem.prototype.type = "checklist";

  return ChecklistItem;

}).call(this);

module.exports = ChecklistItem;
