<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export index = undefined
  export item = undefined
  export listId = undefined
  export sameListOnly = false

  isOver = false
  isInvalid = false
  checkTypesForListId = (types)->
    types.find (t)->
      return t.startsWith("listid:")

  over = (e)->
    e.preventDefault()
    e.stopPropagation()
    isOver = true

    isInvalid = false
    unless !sameListOnly


      draggedListId = checkTypesForListId(e.dataTransfer.types)

      if draggedListId?
        isInvalid = !(listId == draggedListId.replace("listid:",""))
      else
        isInvalid = true
    #if isInvalid
    #  return e.preventDefault()
    #console.log "return is invalid: #{!isInvalid}"
    # return !isInvalid

  leave = (e)->
    draggable = false
    isOver = false
    isInvalid = false


  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false
    e.preventDefault()
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))
    dropped = true
    unless !sameListOnly
      # console.log "This list: #{listId} :: itemList #{dragItem.listId}"
      dropped = (listId == dragItem.listId)


    if dropped
      dispatch "drop",
        divider: true
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: item
        dropIndex: index
        dropListId: listId


</script>
<style lang="sass">
  div
    border-radius: 3px
    width: calc(100% + 96px)
    margin-left: -52px
    background: transparent
    height: 8px
    &.over
      background: #dddddd
    &.invalid
      background: transparent
</style>
<div
  class:over={isOver}
  class:invalid={isInvalid}
  {index}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
/>
