<script lang="coffeescript">

  import {push, pop, replace, location} from 'svelte-spa-router'

  import { createEventDispatcher } from 'svelte'
  dispatch = createEventDispatcher()

  import FileSaver from "file-saver"
  import md5 from "md5"

  import Resource from '../../../helpers/project.js'
  resource = new Resource({})

  import Section from "../../_Common/Sections/_Section.svelte"

  import FilteredList from '../../../components/Lists/FilteredList.svelte'
  import DeleteConfirmation from '../../../components/Overlay/Modal/DeleteConfirmation.svelte'

  import Checkbox from '../../../components/Form/Checkbox/Checkbox.svelte'

  export item = undefined
  export open = true

  showDeleteConfirmation = false

  deleteConfirmation = ()->
    showDeleteConfirmation = true

  deleteConfirmationCancel = ()->
    showDeleteConfirmation = false

  destroy = ()->
    scb = ()->
      console.log "deleted"
      push "/"
    resource.destroy item.id, scb, null, null

  update = (changes)->
    scb =(updated)->
      console.log "updated"
      console.log scb
    ecb = (err)->
      console.log "udpate err"
      console.log err
      console.log "should refresh the proejct"

    resource.update item, scb, ecb, null

  deferredUpdate = update.debounce(500)

  toggleSection = (e)->
    details = e.detail
    item.settings.sections[details.key].enabled = details.checked
    dispatch "update"

  saveAs = ()->
    jsonString = JSON.stringify(item,null,2)
    blob = new Blob([jsonString], {type:  "text/plain;charset=utf-8"})
    # note - the md5 works on the string loaded in the file, which is not the same as the file''s md5
    # use a different handler for electron
    FileSaver.saveAs blob, "project-#{item.name.replace(/[^a-z0-9]/gi, '_').first(50).toLowerCase()}-#{Date.create().toTimestampId()}--#{md5(jsonString)}.json"


  clone = ()->
    newProject = Object.clone(item,true)
    console.log "newProject to be cloned"

    console.log newProject
    newId = uuid()
    newProject.id = newId
    newProject._id = newId
    newProject.name = "#{newProject.name} (clone)"
    delete newProject._rev
    scb = (saved)->
      push "/projects/#{saved.id}"
    console.log newProject
    resource.insert newProject, scb, null,  null


  archiveToggle = ()->
    item.is_archived = !item.is_archived
    update { id: item.id, is_archived: item.is_archived }

</script>

<style lang="sass">

</style>

<Section {open} name="Settings">
<article type="content">

  <h2>Enable/Disable Sections</h2>

  <p>Enabling and disabling will not delete existing items, it will only prevent them from being visible.</p>


  <field>
    <Checkbox checked={item.settings.sections.about.enabled} key="about" label="Enable About" on:change={toggleSection}/>
  </field>
  <field>
    <Checkbox checked={item.settings.sections.assignments.enabled} key="assignments" label="Enable Assignments" on:change={toggleSection}/>
  </field>
  <field>
    <Checkbox checked={item.settings.sections.tasks.enabled} key="tasks" label="Enable Tasks" on:change={toggleSection}/>
  </field>
  <field>
    <Checkbox checked={item.settings.sections.documents.enabled} key="documents" label="Enable Documents" on:change={toggleSection}/>
  </field>
  <field>
    <Checkbox checked={item.settings.sections.clips.enabled} key="clips" label="Enable Clips" on:change={toggleSection}/>
  </field>
  <h2>Common Actions</h2>

  <h3>Clone Collection</h3>
  <p>This will make an exact copy of the collection.</p>

  <p><button type="button" primary on:click={clone}>Clone Collection</button></p>

  <h3>Export Collection</h3>
  <p>This will save your collection as a JSON file into your downloads folder.</p>
  <button type="button" primary on:click={saveAs}>Save to Disk</button>


  <h3>Archive Collection</h3>
  <p>This will mark the item as archived and it will no longer appear in the current items list.</p>
  <p><button type="button" primary on:click={archiveToggle}>{#if item.is_archived}Unarchive{:else}Archive{/if} Collection</button></p>

  <h2>Danger Zone</h2>
  <p>Deleting the item will <b>permanently</b> delete all assignments, tasks and other items related to this item.</p>
  <div>
    <button type="button" critical on:click={deleteConfirmation}>Delete Collection</button>
  </div>
</article>
</Section>
{#if showDeleteConfirmation}
  <DeleteConfirmation on:destroy={destroy} on:cancel={deleteConfirmationCancel}/>
{/if}
