<script lang="coffeescript">

  import AddStub from "./_AddStub.svelte"
  import {createEventDispatcher} from "svelte"
  dispatch = createEventDispatcher()

  export showForm = false
  export stubPlaceholder = "Add item"
  export placeholder = "Enter name here."
  export attribute = "name"
  export isTextArea = false
  import Autogrow from '../../../components/Form/TextArea/Autogrow.svelte'

  stub = {}
  stub[attribute] = ""

  resetStub = ()->
    stub[attribute] = ""

  edit = ()->
    showForm = true

  cancel = ()->
    showForm = false

  add = ()->
    console.log "add click"
    if stub[attribute]?
      if stub[attribute].length > 0
        dispatch "add", stub
        resetStub()

  validate = (val)->
    if val?
      return (val.length > 0)
    else
     false
  keyup = (e)->
    if (e.key == "Return") || (e.key == "Enter")
      add()
    if (e.key == "Escape")
      cancel()

  textareakeyup = (e)->

    if (e.detail.key == "Escape")
      cancel()

  `$: invalid = !validate(stub[attribute])`

</script>

{#if !showForm}
  <AddStub {stubPlaceholder} on:edit={edit}/>
{:else}
  <div layout="row">

    <div flex>
      {#if isTextArea}
        <Autogrow {placeholder} bind:value={stub[attribute]} on:keyup={textareakeyup} autofocus style="width: 100%"/>
      {:else}
        <input type="text" {placeholder} bind:value={stub[attribute]} on:keyup={keyup} autofocus/>
      {/if}
    </div>
    <div flex="initial">
      <button type="button" icon on:click={add} disabled={invalid}>
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 512 512"><title>ionicons-v5-a</title><line x1="256" y1="112" x2="256" y2="400" style="fill:none;stroke:#000;stroke-linecap:round;stroke-linejoin:round;stroke-width:32px"/><line x1="400" y1="256" x2="112" y2="256" style="fill:none;stroke:#000;stroke-linecap:round;stroke-linejoin:round;stroke-width:32px"/></svg>
      </button>
    </div>
    <div flex="initial">
      <button type="button" icon on:click={cancel}>
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"/></svg>
      </button>
    </div>
  </div>

{/if}
