var Comment, SubDocument;

SubDocument = require("./sub_document.js");

Comment = (function() {
  class Comment extends SubDocument {
    model() {
      return {
        // if sourceversion < latest version, then some upgrades may be required
        // if null then return latest
        id: null,
        type: this.type,
        ordinal: null,
        name: "",
        hierarchy: {},
        metadata: {},
        effort_minutes: 0,
        booked_at: null,
        created_at: null,
        updated_at: null,
        versions: []
      };
    }

  };

  Comment.prototype.name = "Comment";

  Comment.prototype.type = "comment";

  return Comment;

}).call(this);

module.exports = Comment;
