<script lang="coffeescript">

  import { slide } from "svelte/transition"

  # open state
  export open = true
  # click on header to toggle open state
  export headerClick = true
  # pass through styles
  export style = undefined
  # pass through classes
  export classes = undefined
  # pass through id
  export id = undefined
  # default transition time
  export transitionDuration = 75

  ###
    slots
      header      summary
      [default]   detail content
  ###

  handleClick = (e)->
    if headerClick
      open = !open

</script>

<div expanded={open} {style} {id} class={classes}>
  <header on:click={handleClick} {open}><slot name="header"/></header>
  {#if open}
    <div transition:slide="{{duration: transitionDuration}}"><slot {open}/></div>
  {/if}
</div>
