var SubDocument, TimeEntry;

SubDocument = require("./sub_document.js");

TimeEntry = (function() {
  class TimeEntry extends SubDocument {
    model() {
      return {
        // if sourceversion < latest version, then some upgrades may be required
        // if null then return latest
        id: null,
        type: this.type,
        name: "",
        effort_minutes: 0,
        is_billable: true,
        booked_at: null,
        assigned_to: null,
        created_at: null,
        updated_at: null,
        metadata: {}
      };
    }

  };

  TimeEntry.prototype.name = "Time Entry";

  TimeEntry.prototype.type = "time_entry";

  return TimeEntry;

}).call(this);

module.exports = TimeEntry;
